@import '../../resources/globalStyles.scss';

.booksFinderContainer {
    @include spacing(0, 1rem);
}

.buttonsWrapper {
    @include flex($justifyContent: center, $gap: 1rem);
}

.inputWrapper {
    @include flex($flexDirection: column, $gap: 1rem);

    @media (min-width: 1024px) {
        flex-direction: row;
    }

    .inputCol {
        @include flex($flexDirection: column, $gap: 0.5rem, $alignItems: flex-start);

        &:first-of-type {
            flex: 1;
        }
    }
}

.loaderWrapper {
    @include position($position: fixed, $top: 50%, $left: 50%, $transform: translate(-50%, -50%));
    z-index: 100;
}
